import SideMenu  from '@/blockComponents/sideMenu'// 
import Toast     from '@/modal/toast'             //  
export default {
    name: "defaultLayout",
    components:{
      SideMenu,   //  ,
      Toast      //  
    },
    data(){
      return{
        login       : true,   //    on
        userName    : '', //    
        closeMenu   : false,  //   of off
        searchActive: false,  //  on off
        scTimer     :0,       //  on off
        scY         :0,       // 
      }
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
    },
    methods:{
      // my fans  alert
      ready(){
        alert('  .')
      },
      //   
      closeBtn(){
        this.closeMenu    = false
        this.searchActive = false
      },
      //  on off
      searchBtn(){
        if(this.searchActive == false )
          this.searchActive = true 
        else
          this.searchActive = false
      },
      //   
      handleScroll(){
        if (this.scTimer) return;
          this.scTimer   = setTimeout(() => {
            this.scY     = window.scrollY;
            clearTimeout(this.scTimer);
            this.scTimer = 0;
          }, 100);
      },
      //   
      toTop(){
        window.scrollTo({
          top: 0,
          behavior: "smooth"
        });
      }
    }
}
